// components/Layout/ThemeToggle.jsx
"use client";

import React from 'react';
import { useTheme } from '../../Context/ThemeContext';

const ThemeToggle = () => {
    const { theme, toggleTheme } = useTheme();

    return (
        <button
            onClick={toggleTheme}
            className="inline-flex h-9 w-9 items-center justify-center rounded-xl border border-slate-300 bg-slate-100 text-slate-800 transition hover:bg-slate-200 dark:border-slate-700 dark:bg-slate-800 dark:text-slate-200 dark:hover:bg-slate-700"
            aria-label={`Switch to ${theme === 'light' ? 'dark' : 'light'} mode`}
        >
            {theme === 'light' ? (
                // Sun Icon (Light Mode - Filled)
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" className="w-5 h-5">
                    <path d="M12 2.25a.75.75 0 01.75.75v2.25a.75.75 0 01-1.5 0V3a.75.75 0 01.75-.75zM7.5 12a4.5 4.5 0 119 0 4.5 4.5 0 01-9 0zM18.894 6.183a.75.75 0 00-1.06-1.06L15.474 6.782a.75.75 0 001.06 1.06l2.36-2.36zM15.474 17.218a.75.75 0 00-1.06 1.06l2.36 2.36a.75.75 0 001.06-1.06l-2.36-2.36zM4.5 15.75a.75.75 0 00.75.75h2.25a.75.75 0 000-1.5H5.25a.75.75 0 00-.75.75zM17.25 12a.75.75 0 00.75.75h2.25a.75.75 0 000-1.5H18a.75.75 0 00-.75.75zM5.25 6a.75.75 0 00.75.75h2.25a.75.75 0 000-1.5H6a.75.75 0 00-.75.75zM12 18a.75.75 0 01.75-.75h2.25a.75.75 0 010 1.5H12.75a.75.75 0 01-.75-.75zM6.782 15.474a.75.75 0 00-1.06-1.06l-2.36 2.36a.75.75 0 001.06 1.06l-2.36-2.36z" />
                </svg>
            ) : (
                // Moon Icon (Dark Mode - Outline)
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-5 h-5">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M21.752 15.002A9.718 9.718 0 0112 21.75c-5.523 0-9.999-4.477-9.999-9.999 0-1.285.247-2.522.695-3.655M2.25 6.25l18.498 18.498M18.75 3a.75.75 0 00-.75-.75h-2.5a.75.75 0 00-.75.75V4a.75.75 0 00.75.75h2.5a.75.75 0 00.75-.75V3z" />
                    <path strokeLinecap="round" strokeLinejoin="round" d="M12 1.5a.75.75 0 01.75.75V4.5a.75.75 0 01-1.5 0V2.25a.75.75 0 01.75-.75zM18.75 3a.75.75 0 00-.75-.75h-2.5a.75.75 0 00-.75.75V4a.75.75 0 00.75.75h2.5a.75.75 0 00.75-.75V3zM12 18a.75.75 0 01.75-.75h2.25a.75.75 0 010 1.5H12.75a.75.75 0 01-.75-.75zM5.25 4.5a.75.75 0 01.75.75V6a.75.75 0 01-1.5 0V5.25a.75.75 0 01.75-.75zM12 18a.75.75 0 01.75-.75h2.25a.75.75 0 010 1.5H12.75a.75.75 0 01-.75-.75zM6.782 15.474a.75.75 0 00-1.06-1.06l-2.36 2.36a.75.75 0 001.06 1.06l-2.36-2.36z" />
                </svg>
            )}
        </button>
    );
};

export default ThemeToggle;